<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6" v-for="(site,itemIndex) in vueRecommend" :key="itemIndex">
        <el-card shadow="hover" class="box-card" @click="">
          <a :href="site.siteUrl" target="_blank">
            <img :src="site.siteIcon" class="card-icon">
            <div class="site-content">
              <div class="site-title" :title="site.siteTitle">
                {{site.siteTitle}}
              </div>
              <div class="site-description" :title="site.siteDescription">
                {{site.siteDescription}}
              </div>
            </div>
          </a>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'MainContent',
  data() {
    return {
      vueRecommend: [
        {
          siteIcon: require("@/assets/img/web/base/bootstrap.png"),
          siteTitle: 'Bootstrap',
          siteDescription: '简洁、直观、强悍的前端开发框架，让web开发更迅速、简单',
          siteUrl: "https://www.bootcss.com/"
        },
      ]
    }
  },
}
</script>

<style>
a {
  text-decoration: none;
  color: #373e4a;
}

.card-icon {
  height: 40px;
  width: 40px;
  float: left;
  margin-right: 10px;
}

.box-card {
  border-radius: 12px;
  height: 90px;
  margin-top: 15px;
  /* display: flex; */
}

.site-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.site-description {
  color: #979898;
  font-size: 13px;
  margin: 2px;
  word-break: break-word;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
</style>


